.title-component {
  margin: 0 auto;
}
.page-content-subtitle {
  width: 100%;
  font-size: 13px;
  line-height: 20px;
  margin-bottom: 20px;
  text-align: center;
}
.page-content-title {
  @media screen and (max-width: 768px) {
    // width: 100%;
    font-size: 32px;
    text-align: center !important;
  }
  width: 100%;
  font-size: 45px;
  font-weight: 700;
  line-height: 50px;
  text-align: center;
}
.page-content {
  @media screen and (max-width: 768px) {
    text-align: center !important;
  }
  width: 100%;
  font-size: 20px;
  line-height: 25px;
  word-break: break-word;
  margin: auto;
  > div {
    margin-bottom: 30px;
    &:last-child {
      margin-bottom: 0;
    }
  }
}

.title-wrap {
  @media screen and (max-width: 768px) {
    width: 100% !important;
    padding: 0 15px;
    word-break: break-word;
  }
}
