.description-area {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 100px 0 0 0;
  @media (max-width: 768px) {
    display: block;
    .description-item {
      width: calc(100% - 40px) !important;
      height: 260px !important;
      margin: 0 20px 30px !important;
      .title {
        width: 100% !important;
      }
    }
  }

  .description-item {
    // background: #0033cc;
    padding: 24px 28px;
    color: #f0f0f0;
    margin: 0 0 10px;

    .title {
      font-style: normal;
      font-weight: 900;
      font-size: 20px;
      line-height: 30px;
      margin: 0 0 18px;
      color: #1e1e1e;
    }

    .description {
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 25px;
      color: #1e1e1e;
    }

    img {
      height: 50px;
      vertical-align: middle;
      align-self: center;
      &:first-child {
        height: 36px;
      }
      &:nth-child(3) {
        height: 44px;
      }
    }
  }
}
